.breadcrumb {
    line-height: 20px !important;
    line-height: rem(20px) !important;
    padding: 6px 0 !important;
    margin-bottom: 0;
    margin-top: 0;

    & :not(:last-child) {
        @include iconMdsp(navigateArrow, after);
    }
    & :not(:last-child):after {
        color: $gray200;
        font-size: 24px;
        font-size: rem(24px);
        vertical-align: bottom;
    }

    .breadcrumb__item {
        color: $gray400;
        display: inline-block;
        font-family: "Siemens Sans Roman", sans-serif;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 20px;
        line-height: rem(20px);
        margin: 0;

        &:hover {
            cursor: default;
        }

        > a {
            color: $stateBlue100;
            cursor: pointer;
            text-decoration: none;
            @include hoverTransition(color);
        }

        > a:hover {
            color: $coolBlue;
            text-decoration: underline;
        }
        
        > a:active {
            color: $stateBlue250;
            text-decoration: underline;
        }

        &.is-current,
        &.is-current a,
        &.is-current a:visited,
        &.is-current a:hover {
            color: $gray400;
            text-decoration: none;
            cursor: default;
        }
    }
}


//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    .breadcrumb {
        margin-block-start: 0;
        margin-block-end: 0;
    }
}
//endRemoveIf(sassHandover)

