.dialog {
    display: none;
    z-index: 10;

    &.is-shown {
        display: flex;
        .dialog__overlay {
            opacity: 1;
        }
    }

    & ,
    &__overlay {
        align-items: center;
        box-sizing: border-box;
        height: 100%;
        justify-content: center;
        left: 0;
        position: fixed;
        top: 0;
        width: 100%;
    }

    &__overlay {
        background-color: rgba($gray800, 0.7);
        opacity: 0;
        z-index: -1;
    }

    &__container {
        align-items: center;
        box-sizing: border-box;
        display: flex;
        flex-direction: row;
        height: 100%;
        justify-content: space-around;
        pointer-events: none;
        width: 100%;
    }

    &__background {
        background-color: $white;
        border-radius: 3px;
        box-shadow: rgba(0,0,0,0.5) 0px 2px 30px 0px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        flex-grow: 0;
        flex-shrink: 0;
        max-height: 100%;
        overflow-y: auto;
        pointer-events: auto;
        width: 600px;

        @include breakpoint($xs) {
            border-radius: 0;
            width: 100%;
            height: 100%;
        }
    }

    &__title {
        border-bottom: 1px solid $gray200;
        box-sizing: border-box;
        color: $gray800;
        flex-shrink: 0;
        font-size: 21px;
        line-height: 29px;
        margin: 0;
        min-height: 44px;
        padding: 7px 50px 7px 16px;
        position: relative;

        .dialog__close {
            box-sizing: border-box;
            display: inline-block;
            height: 24px;
            position: absolute;
            top: 10px;
            right: 10px;
            width: 24px;

            &:hover {
                &:before, &:after {
                    background-color: $coolBlue;
                }
            }

            &:before, &:after {
                background-color: $gray800;
                content: '';
                display: block;
                height: 20px;
                left: 50%;
                margin-left: -1px;
                margin-top: -10px;
                position: absolute;
                top: 50%;
                transform: rotate(-45deg);
                transition: all 0.2s ease-in-out;
                width: 2px;
                @include hoverTransition(background-color);
            }

            &:after {
                transform: rotate(-135deg);
            }
        }
    }

    &__content {
        flex-grow: 1;
        margin: 0;
        overflow: auto;
        padding: 24px 32px;

        img {
            height: auto;
            max-width: 100%;
            vertical-align: text-bottom; // remove vertical spacing below bottom
        }

        p:last-child {
            margin-bottom: 0;
        }
    }

    &__footer {
        align-items: center;
        border-top: 1px solid $gray200;
        box-sizing: border-box;
        display: flex;
        flex-shrink: 0;
        flex-wrap: wrap;
        justify-content: flex-end;
        margin: 0;
        min-height: 64px;
        padding: 16px 16px 0 16px;
        position: relative;

        .button, button {
            margin-bottom: 16px;
            margin-top: 0;
        }
    }

    // width- and height-related properties are following not sorted alphabetically but better readable manner
    &.dialog--medium {
        .dialog__background {
            border-radius: 0;
            width: 100%;
            max-width: 960px;
            @include breakpoint($sm) {
                border-radius: 3px;
                width: 60%;
                height: auto;
                min-height: auto;
                max-height: 60%;
            }
            @include breakpoint($md) {
                border-radius: 3px;
                width: 60%;
                max-width: 960px;
                height: auto;
                min-height: auto;
                max-height: 60%;
            }
            @include breakpoint($lg) {
                border-radius: 3px;
                width: 60%;
                max-width: 960px;
                height: auto;
                min-height: auto;
                max-height: 60%;
            }
            @include breakpoint($xl) {
                border-radius: 3px;
                width: 960px;
                height: auto;
                min-height: auto;
                max-height: 60%;
            }
        }
    }
    
    &.dialog--large {
        .dialog__background {
            border-radius: 0;
            width: 100%;
            max-width: 1920px;
            @include breakpoint($sm) {
                width: 100%;
                height: 100%;
            }
            @include breakpoint($md) {
                border-radius: 3px;
                width: 90%;
                max-width: 1920px;
                height: auto;
                min-height: auto;
                max-height: 90%;
            }
            @include breakpoint($lg) {
                border-radius: 3px;
                width: 90%;
                max-width: 1920px;
                height: auto;
                min-height: auto;
                max-height: 90%;
            }
            @include breakpoint($xl) {
                border-radius: 3px;
                width: 1920px;
                height: auto;
                min-height: auto;
                max-height: 90%;
            }
        }
    }

    &.dialog--frameless {
        .dialog__content {
            padding: 0;
        }
    }
}