@font-face {
    font-family: "mdspcss";
    font-display: auto;
    src: url('#{$assetPath}#{$webfontsPath}mdspcss.eot');
    src: url('#{$assetPath}#{$webfontsPath}mdspcss.eot?#iefix') format('eot'),
    url('#{$assetPath}#{$webfontsPath}mdspcss.woff2') format('woff2'),
    url('#{$assetPath}#{$webfontsPath}mdspcss.woff') format('woff'),
    url('#{$assetPath}#{$webfontsPath}mdspcss.ttf') format('truetype');
}