.table {
    border-collapse: collapse;
    font-family: $siemensFontRegular;
    margin: 12px 0 16px 0;
    margin: rem(12px 0 16px 0);
    max-width: 100%;
    table-layout: fixed;
    width: 100%;

    th {
        font-family: $siemensFontBold;
        font-weight: normal;
    }

    td, th {
        border-bottom: 1px solid $gray200;
        font-size: 14px;
        font-size: rem(14px);
        overflow: hidden;
        padding: 6px 0 6px 6px;
        padding: rem(6px 0 6px 6px);
        text-align: left;
        text-overflow: ellipsis;

        &:first-child {
            padding-left: 24px;
        }

        &:last-child {
            padding-right: 24px;
            width: auto;
        }

        .th__description,
        .td__description {
            color: $gray400;
            font-size: 12px;
            font-size: rem(12px);
        }
    }

    // this property is necessary for other frameworks which set a default border-top for
    // headline cells, such as Twitter Bootstrap
    td,
    th {
        border-top: 0 transparent;
    }

    &.table--s {
        td, th {
            padding: 6px 0 6px 12px;
            padding: rem(6px 0 6px 12px);
        }
    }

    &.table--leading {
        tbody {
            border-top: 1px solid $gray200;

            tr > td:first-child {
                font-family: $siemensFontBold;
            }
        }
    }

    &.table--alternate {
        tbody tr:nth-child(odd) {
            background-color: rgba($gray100, 0.5);
        }

        tbody tr:hover {
            background-color: $stateBlue;
        }
    }
}

//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    table {
        @extend .table;
    }
}
//endRemoveIf(sassHandover)