.tabs--vertical {
  background: $white;
  border: 1px solid $gray200;
  display: flex;
  flex-wrap: nowrap;

  .tab__nav {
    background: $gray100;
    border-right: 1px solid $gray200;
    max-width: 370px;
    min-width: 270px;
    width: 30%;

    @include breakpoint($xs) {
      max-width: 50%;
      min-width: 50%;
      width: 50%;
    }

    > ul {
      border-bottom: 1px solid $gray200;
      list-style-type: none;
      margin: 0 0 -1px 0;
      padding: 0;
    }
  }

  .tab__item {
    margin: 0;

    &:last-child > a {
      border-bottom: none;

      &.is-activated, &:active {
        border-bottom: 1px solid $gray200;
      }
    }

    a {
      border-bottom: 1px solid $gray200;
      border-right: 1px solid $gray200;
      color: $stateBlue100;
      display: block;
      font-family: $siemensFontRegular;
      font-size: 14px;
      font-size: rem(14px);
      line-height: 24px;
      line-height: rem(24px);
      margin-right: -1px;
      padding: 12px 16px;
      text-decoration: none;

      &:hover {
        background: $stateBlue;
        color: $stateBlue100;
      }

      &.is-activated {
        background: $white;
        border-right: 1px solid $white;
        color: $gray800;
      }

      &:active {
        background: $white;
        color: $gray800;
      }

      &:focus {
        background: $white;
        box-shadow: inset 0 0 0 2px $coolBlue;
        // IE11 does not support outline-offset
        // outline: 2px solid $coolBlue;
        // outline-offset: -2px;
        outline: 0;
      }

    }
  }

  .tab__headline {
    align-items: center;
    display: flex;
    font-family: $siemensFontBold;
    line-height: 24px;
    line-height: rem(24px);

    .iconMdsp {
      font-size: 24px;
      font-size: rem(24px);
      margin-right: 4px;
      margin-right: rem(4px);
    }
  }

  .tab__description {
    color: $gray400;
    display: block;
    font-size: 12px;
    font-size: rem(12px);
    line-height: 16px;
    line-height: rem(16px);
    margin-top: 4px;
  }

  .tab__content {
    padding: 24px;
    padding: rem(24px);
  }
}