.message {
    background-color: $gray100;
    border-radius: 3px;
    border: 1px $gray200 solid;
    box-sizing: border-box;
    color: $gray800;
    display: inline-block;
    font-family: $siemensFontRegular;
    font-size: 14px;
    font-size: rem(14px);
    font-weight: normal;
    line-height: 20px;
    line-height: rem(20px);
    margin: 8px 8px 8px 0;
    margin: rem(8px 8px 8px 0);
    padding: 12px 16px 16px;
    padding: rem(12px 16px 16px);
    position: relative;
    vertical-align: top;
    width: 100%;

    &.has-contentWidth {
        width: auto;
    }

    ol, ul {
        padding-bottom: 0;
    }

    &--withIcon {
        @include iconMdsp(exclamationmarkCircleInverted);
        &:before {
            font-size: 24px;
            font-size: rem(24px);
            font-style: normal;
            font-variant: normal;
            font-weight: normal;
            left: 16px;
            left: rem(16px);
            line-height: 24px;
            line-height: rem(24px);
            position: absolute;
            top: calc(50% - 12px);
        }

        &:after {
            border-right: 1px solid $gray200;
            content: '';
            height: 100%;
            left: 56px;
            left: rem(56px);
            position: absolute;
            top: 0;
            width: 0;
        }

        &:not(.message--s) {
            > * {
                margin-left: 56px;
                margin-left: rem(56px);
            }
        }
    }

    &--s {
        padding: 6px 12px;
        padding: rem(6px 12px);

        &.message--withIcon {
            &:before {
                left: 10px;
                left: rem(10px);
            }

            &:after {
                border: none;
            }

            > * {
                padding: 0;
                padding-left: 30px;
                padding-left: rem(30px);
            }
        }
    }

    > *,
    > p {
        margin: 0;
        margin-bottom: 8px;
        margin-bottom: rem(8px);
    }

    > :last-child {
        margin-bottom: 0;
    }

    > h1,
    > .message__headline {
        font-family: $siemensFontBold;
        font-size: 14px;
        font-size: rem(14px);
        line-height: 20px;
        line-height: rem(20px);
        margin-bottom: 0;
    }

    a,
    button,
    .button--primary,
    .button--secondary,
    .button--ghost {
        margin-top: 0;
        text-decoration: none;
    }

    label {
        cursor: pointer;
        @include iconMdsp(arrowGoTo, after);

        &:after {
            padding-left: 4px;
            padding-left: rem(4px);
        }
    }

    &.is-informative {
        background-color: $contextBlue050;
        border: 1px solid $contextBlue150;
        color: $contextBlue600;

        &.message--withIcon {
            @include iconMdsp(infoCircleInverted);
        }
        &.message--withIcon:after {
            border-color: $contextBlue150;
        }
    }

    &.is-success {
        background-color: $contextGreen050;
        border: 1px solid $contextGreen200;
        color: $contextGreen600;

        &.message--withIcon {
            @include iconMdsp(checkInverted);
        }
        &.message--withIcon:after {
            border-color: $contextGreen200;
        }
    }

    &.is-warning {
        background-color: $contextYellow;
        border: 1px solid $contextYellow100;
        color: $contextYellow600;

        &.message--withIcon {
            @include iconMdsp(exclamationmarkDiamondInverted);
        }
        &.message--withIcon:after {
            border-color: $contextYellow100;
        }
    }

    &.is-alert {
        background-color: $contextRed;
        border: 1px solid $contextRed200;
        color: $contextRed500;

        &.message--withIcon {
            @include iconMdsp(exclamationmarkInverted);
        }
        &.message--withIcon:after {
            border-color: $contextRed200;
        }
    }
}