.badge {
    border-radius: 15px;
    display: inline-block;
    font-family: $siemensFontBold;
    // we do not set rem here on purpose since the badges shall not scale with increasing font size (scaling problems with icon positioning which are bigger then font size of badges)
    font-size: 11px;
    line-height: 16px;
    margin-left: 4px;
    margin-right: 4px;
    padding: 2px 6px 2px 6px;
    position: relative;
    vertical-align: baseline;
    white-space: nowrap;

    &.badge--button {
        padding: 4px 10px 4px 8px; // padding-right of 10px to compansate the negative margin-right of the badge
    }

    a {
        color: inherit !important;
        display: inline-block;
        line-height: 24px;
        margin-bottom: -4px;
        margin-right: -10px;
        margin-top: -4px;
        text-decoration: none !important;
        width: 24px;

        .iconMdsp {
            display: inline-block;
            font-size: 24px;
            vertical-align: bottom;
        }
    }

}


.badge--dark {
    background-color: $gray400;
    color: $white;
}

.badge--light {
    background-color: $gray200;
    color: $gray800;
}

.button {
    .badge {
        font-size: 11px;
        margin-bottom: -4px;
        margin-top: -2px;
        padding: 2px 6px;
        top: -1px;
        vertical-align: baseline;
    }
}