.pager {
    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    &.has-textalign-right {
        text-align: right;
    }

    &__page {
        border-radius: 3px;
        color: $gray400;
        display: inline-block;
        font-size: 14px;
        font-size: rem(14px);
        letter-spacing: 0.09px;
        letter-spacing: rem(0.09px);
        line-height: 18px;
        line-height: rem(18px);
        margin: 0;
        padding: 0;
        text-align: center;
        vertical-align: middle;

        a {
            @extend .button;
            @extend .button--ghost;
            margin: 0 0 0 2px !important;
            margin: rem(0 0 0 2px !important);
            min-width: 20px;
            min-width: rem(20px);
            padding: 8px 6px;
            padding: rem(8px 6px);
            text-decoration: none !important;
        }

        &.is-current a {
            background-color: $stateBlue050;
        }
    }

    &__pageCounter {
        margin-left: 8px !important;
        margin-left: rem(8px !important);
    }

    &__itemPerPage {
        margin-left: 15% !important;
    }

    &.pager--advanced {
        padding: 5px 0;
        padding: rem(5px 0);

        .inputGroup {
            margin: 0 8px !important;
            margin: rem(0 8px !important);

            .selectWrapper {
                display: inline-block;
                margin-left: 0.5em;
                vertical-align: middle;
            }
        }
        // todo: these are styled also when no forcedefaults are present
        input,
        select {
            border: 1px solid $gray200;
            box-shadow: none;
            padding: 6.5px 11px;
            padding: rem(6.5px 11px);
        }
        input {
            max-width: 64px;
            max-width: rem(64px);
            text-align: center;
        }
        select {
            padding-right: 26px;
            padding-right: rem(26px);
        }
    }

    &.pager--withBorder {
        border-bottom: 1px solid $gray200;
        border-top: 1px solid $gray200;
        padding: 5px 20px;
        padding: rem(5px 20px);
    }
}