.button,
a.button {

    &:link,
    &:visited {
        color: $white;
    }

    border-radius: 3px;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;

    font-family: $siemensFontRegular;
    font-size: 14px;
    font-size: rem(14px);
    line-height: 14px;
    line-height: rem(14px);
    margin: 8px 0px;
    margin: rem(8px 0px);
    padding: 8px 16px;
    padding: rem(8px 16px);
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    white-space: nowrap;

    @include hoverTransition($animate: (background-color, color, border));

    & + .button {
        margin-left: 8px;
    }

    &[type="submit"] { // disable 'native' IOS look on iPhones
        -webkit-appearance: none;
    }

    &:focus {
        outline: 2px solid $coolBlue;
        outline-offset: -2px;
    }

    &.button--primary {
        background-color: $stateBlue100;
        border: 1px solid transparent;
        color: $white !important;
        text-decoration: none;

        &:link, &:visited {
            text-decoration: none;
        }

        &:hover {
            background-color: $coolBlue;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue250;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $gray400;
            color: $white;
            &:hover {
                cursor: not-allowed;
            }
        }
    }

    &.button--secondary {
        background-color: transparent;
        border: 1px solid $stateBlue100;
        color: $stateBlue100;
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $stateBlue;
            border: 1px solid transparent;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue050;
            border: 1px solid transparent;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            border-color: $gray400;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--ghost {
        background-color: transparent;
        color: $stateBlue100;
        font-family: $siemensFontRegular;
        border: 1px solid transparent;
        padding: 8px;
        padding: rem(8px);
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $stateBlue;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue050;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--contentAction {
        background-color: transparent;
        border: 1px solid transparent;
        color: $stateBlue100;
        font-family: $siemensFontBold;
        margin: 0 8px;
        padding: 8px 2px 8px 0px;
        padding: rem(8px 2px 8px 0px);
        position: relative;
        text-decoration: none;

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            color: $coolBlue;
            text-decoration: none;
        }

        &:focus {
            outline-offset: 0px;
        }

        &:active {
            color: $stateBlue250;
            outline: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: transparent;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    &.button--split {
        background-color: $white;
        border-radius: 0;
        border: none;
        border-bottom: 1px solid rgba(0,0,0,0.26);
        color: $gray800;
        display: block;
        margin: 0;
        text-align: left;

        &:last-child {
            border-bottom: none;
        }

        &:link, &:visited {
            color: $stateBlue100;
            text-decoration: none;
        }

        &:hover {
            background-color: $stateBlue;
            text-decoration: none;
        }

        &:active {
            background-color: $stateBlue050;
            outline: none;
            text-decoration: none;
        }

        &:disabled,
        &[disabled],
        &.is-disabled {
            background-color: $white;
            border-color: $gray400;
            color: $gray400;
            cursor: not-allowed;
        }
    }

    .iconMdsp {
        display: inline-block;
        font-size: 24px;
        font-size: rem(24px);
        line-height: 14px;
        line-height: rem(14px);
        margin: 0 4px 0 -2px;
        margin: rem(0 4px 0 -2px);
        vertical-align: bottom;
    }

    &.has-icon-right {
        .iconMdsp {
            margin-left: 4px;
            margin-left: rem(4px);
            margin-right: 0;
        }
    }

    &.has-icon-only {
        padding: 8px 3px;
        padding: rem(8px 3px);
        .iconMdsp {
            margin-left: 0;
            margin-right: 0;
        }
    }
}

.buttonGroup {
    display: inline-flex;
    flex-wrap: wrap;
    margin: 4px 0 4px 0; // moved half of the vertical margin (4px) to the wrapper. If the button group breaks, it has than a vertical spacing of 4px, instead of 8px. The outer padding of the wrapper is still 8px
    margin: rem(4px 0 4px 0);
    vertical-align: middle;

    + .buttonGroup {
        margin-left: 12px;
        margin-left: rem(12px);
    }

    .button,
    a.button {
        margin: 4px 0;
        margin: rem(4px 0);

        &.button--secondary {
            border-radius: 0;

            &:hover {
                border: 1px solid $stateBlue100;
            }

            &:not(:first-child) {
                margin-left: -1px; // for compensating 1px border
            }

            &:first-child {
                border-top-left-radius: 3px;
                border-bottom-left-radius: 3px;
            }

            &:last-child {
                border-bottom-right-radius: 3px;
                border-top-right-radius: 3px;
            }

            &:active,
            &.is-activated {
                background-color: $stateBlue050;
                border: 1px solid $stateBlue100;
                outline: none;
                text-decoration: none;
            }

            &:disabled,
            &[disabled],
            &.is-disabled {
                background-color: transparent;
            }
        }

        &.button--ghost {
            &:active,
            &.is-activated {
                background-color: $stateBlue050;
                outline: none;
                text-decoration: none;
            }

            &:disabled,
            &[disabled],
            &.is-disabled {
                background-color: transparent;
            }
        }
    }
}

.buttonSplitWrapper {
    align-items: flex-end;
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-end;

    + .buttonSplitWrapper {
        margin-left: 12px;
        margin-left: rem(12px);
    }

    .buttonGroup {
        margin-bottom: 0;
    }

    .buttonSplit {
        border: 1px solid rgba(0,0,0,0.26);
        display: none;

        &.is-activated {
            display: inline-flex;
            flex-direction: column;
            position: relative;

            // fake perspective shadow
            &:after {
                bottom: 0;
                box-shadow: 0 0 12px 0 rgba(50,50,50,0.8);
                content: '';
                display: block;
                height: 60px;
                position: absolute;
                left: 8%;
                right: 8%;
                z-index: -1;
            }
        }
    }
}

//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    button,
    button[type=button],
    input[type=button],
    input[type=reset],
    input[type=submit] {
        @extend .button;
    }
    button + .button,
    button + button,
    .button + button {
        margin-left: 8px;
    }
}
//endRemoveIf(sassHandover)
