$navigationMinWidth: 320px;
$navigationWidth: 320px;

$appBarWidth: 80px;
$appBarWidthSmall: 54px;

.appWrapper {
    bottom: 0;
    box-sizing: border-box;
    left: 0;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 0;

    .appWrapper__appBar {
        background-color: $gray100;
        bottom: 0;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        list-style-type: none;
        margin: 0;
        padding-left: 0;
        position: absolute;
        top: 0;
        width: $appBarWidth;
        @include breakpoint(($xs, $sm)) {
            width: $appBarWidthSmall;
        }
        z-index: 2;

        &:after {
            bottom: 0;
            box-shadow: -8px 0 9px -7px rgba(0,0,0, 0.24) inset;
            content: '';
            display: block;
            position: absolute;
            right: 0;
            top: 0;
            width: 5px;
            z-index: 3;
        }
    }

    .appWrapper__main {
        box-sizing: border-box;
        left: $appBarWidth;
        @include breakpoint(($xs, $sm)) {
            left: $appBarWidthSmall;
        }
        &--noAppBar {
            left: 0;
        }
    }
}


.main {
    bottom: 0;
    position: absolute;
    right: 0;
    top: 0;

    .main__navigation {
        background-color: $gray100;
        border-right: 1px solid $gray250;
        border-left: 1px solid $gray250;
        bottom: 0;
        box-shadow: none;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        left: 0;
        min-width: $navigationMinWidth;
        overflow-x: hidden;
        overflow-y: auto;
        position: absolute;
        top: 0;
        transition: left 0.3s cubic-bezier(0.725, 0.000, 0.300, 1.000);
        width: $navigationWidth;
        z-index: 1;

        @supports (-webkit-overflow-scrolling: touch) {
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
        }

        @include breakpoint($sm) {
            box-shadow: rgba(0,0,0, 0.24) 0 0 2px;
        }

        &.is-collapsed {
            left: -$navigationWidth;

            & + .navigation__toggle + .main__region {
                padding-left: 32px;
            }
        }

    }
    .main__region {
        background-color: $white;
        bottom: 0;
        box-sizing: border-box;
        margin-top: 0;
        overflow-x: hidden;
        overflow-y: auto;
        padding: 16px 32px 0 352px;
        position: absolute;
        top: 0;
        transition: padding-left 0.3s cubic-bezier(0.725, 0.000, 0.300, 1.000);
        width: 100%;
        @supports (-webkit-overflow-scrolling: touch) {
            overflow-y: scroll;
            -webkit-overflow-scrolling: touch;
        }
        @include breakpoint($sm) {
            padding-left: 32px;
        }
    }
}

.appBar__item {
    box-sizing: border-box;
    border-bottom: 1px solid $gray250;
    display: block;
    flex: 1;
    flex-basis: auto;
    @include breakpoint(($xs, $sm)) {
        flex-basis: 50px;
    }
    flex-grow: 0;
    flex-shrink: 0;
    justify-content: flex-start;
    margin: 0 !important;
    text-align: center;

    .iconMdsp {
        color: $stateBlue100;
        display: block;
        font-size: 24px;
    }

    .item__title {
        color: $stateBlue100;
        display: block;
        font-size: 12px;
        line-height: 13px;
    }

    > a {
        box-sizing: border-box;
        color: $stateBlue100;
        display: block;
        min-height: 74px;
        padding: 20px 5px 6px 5px;
        @include breakpoint(($xs, $sm)) {
            min-height: 50px;
            padding-top: 14px;
        }
        &, &:visited, &:active, &:focus {
            text-decoration: none;
        }
    }
    > a:hover {
        text-decoration: none;
    }

    &.is-activated {
        background-color: $gray200;
        > a,
        .iconMdsp,
        .item__title {
            color: $gray800;
        }
    }

    @include breakpoint(($xs, $sm)) {
        .iconMdsp {}
        .item__title {
            display: none;
        }
    }
}

.main .navigation__toggle {
    bottom: 16px;
    left: -70px;
    @include breakpoint(($xs, $sm)) {
        left: -43px;
    }
    position: absolute;
    transition: left 0.3s ease-in-out;
    z-index: 5;

    .button {
        background-color: $gray100;
        border: 1px solid $gray400;
        font-size: 24px;
        margin: 0;
        text-decoration: none !important;
        @include iconMdsp(sidebarHide);

        &:before {
            color: $gray800;
        }

        @include breakpoint(($xs, $sm)) {
            padding-left: 3px;
            padding-right: 3px;
        }
    }
}

.main .main__navigation.is-collapsed + .navigation__toggle {

    .button {
        @include iconMdsp(sidebarShow);
    }

}
