.form  {
    margin: 16px 32px 24px;

    &__formGroup {
        align-content: flex-start;
        align-items: stretch;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-bottom: 24px;
        position: relative;

        .form__lineBreak {
            width: 100%;
        }

        > .inputGroup {
            align-self: auto;
            flex-basis: 240px;
            flex-basis: rem(240px);
            flex-grow: 0;
            flex-shrink: 0;
            margin-right: 8px;
            margin-bottom: 0;

            &--xxs {
                flex-basis: 56px;
                flex-basis: rem(56px);
                max-width: 100%;
            }

            &--xs {
                flex-basis: 80px;
                flex-basis: rem(80px);
                max-width: 100%;
            }

            &--s {
                flex-basis: 160px;
                flex-basis: rem(160px);
                max-width: 100%;
            }

            &--sm {
                flex-basis: 240px;
                flex-basis: rem(240px);
                max-width: 100%;
            }

            &--m {
                flex-basis: 320px;
                flex-basis: rem(320px);
                max-width: 100%;
            }

            &--ml {
                flex-basis: 400px;
                flex-basis: rem(400px);
                max-width: 100%;
            }

            &--l {
                flex-basis: 640px;
                flex-basis: rem(640px);
                max-width: 100%;
            }
        }
    }
}
//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    form {
        @extend .form;
    }
}
//endRemoveIf(sassHandover)