.dialog--confirm {
    .dialog__background {
        // needed because otherwise cannot be styled with forced bg-color-helper classes
        border-top-width: 8px !important;
        border-left-width: 0 !important;
        border-right-width: 0 !important;
        border-bottom-width: 0 !important;

        position: relative;
        .dialog__title {
            display: none;
        }
    }

    &,
    &.confirm--warning {
        .dialog__background {
            border-top: 8px solid $functionalYellow;
        }
        .dialog__content {
            min-height: 16px; // just in case a confirm is empty (missing localization, ...) so it does not collapse
            @include iconMdsp(exclamationmarkDiamondInverted);
            &:before {
                color: $functionalYellow;
            }
        }
        .confirm__icon {
            display: none; // just in case somebody wants to show an arbitrary icon in a default confirm
        }
    }
    &.confirm--alert {
        .dialog__background {
            border-top: 8px solid $functionalRed;
        }
        .dialog__content {
            @include iconMdsp(exclamationmarkInverted);
            &:before {
                color: $functionalRed;
            }
        }
    }
    &.confirm--info {
        .dialog__background {
            border-top: 8px solid $coolBlue;
        }
        .dialog__content {
            @include iconMdsp(exclamationmarkCircleInverted);
            &:before {
                color: $coolBlue;
            }
        }
    }
    &.confirm--custom {
        .dialog__content {
            &:before {
                display: none;
            }
        }
        .confirm__icon {
            color: $gray400;
            font-size: 64px;
            font-size: rem(64px);
            font-style: normal;
            font-variant: normal;
            font-weight: normal;

            position: absolute;
            left: 24px;
            top: 0;
            bottom: 0;

            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 65px;
            @include breakpoint($xs) {
                display: block;
                height: 64px;
                margin-bottom: 0;
                padding-top: 24px;
                padding-left: 24px;

                position: static;
                bottom: auto;
                left: auto;
                top: auto;
            }
        }
    }

    .dialog__content {
        color: $gray800;
        padding: 24px;
        margin-left: 88px;
        margin-left: rem(88px);
        @include breakpoint($xs) {
            margin-left: 0;
            padding-top: 24px;
        }

        &:before {
            font-size: 64px;
            font-size: rem(64px);
            font-style: normal;
            font-variant: normal;
            font-weight: normal;

            position: absolute;
            left: 24px;
            top: 0;
            bottom: 0;

            align-items: center;
            display: flex;
            justify-content: center;
            margin-bottom: 65px;
            @include breakpoint($xs) {
                display: block;
                margin-bottom: 12px;

                position: static;
                bottom: auto;
                left: auto;
                top: auto;
            }
        }

        .confirm__headline, h1 {
            color: $gray800;
            font-family: $siemensFontRegular;
            font-size: 21px;
            font-size: rem(21px);
            line-height: 29px;
            line-height: rem(29px);
            margin-bottom: 8px;
        }
    }
}